body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",   "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",   sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",   monospace;
}

.root{
  position: relative;
  top: 0px;
  left: 0px;
  bottom: 0px;
  height: 100%;
  width: 100%;

}

.Sidebar {
  width: 100%;
  height: 100%;
  position: absolute;
  background: #182026;
}


.tabContainer{
  padding: 10px;
  /*
  background-color: rgb(36, 39, 48);
  */
  height: auto;
  display: block;
  visibility: visible;
}

.tabVisible {
  display: none;
  visibility: hidden;
}

h1 {
  font-weight: 100px;
}

.clear{
  clear: both;
}

.space {
  margin-bottom: 10px;
  padding-top: 10px;
}

.rect {
  border-radius: 0px;
  margin: 20px 0px;
}

.dividerBorder {
  padding: 0px;
  margin: 0px 0px;
  border-bottom: 1px solid #3A414C;
}


.centerOverlay{
  width: 400px;
  top: 0px;
  margin-top: 10vh;
  left: calc(-200px + 50%);
}

.filter-item-contanier {
  width: 100%;
  padding:10px 0px;
  height: 100px;
  background: #182026;
}


.filter-trigger-button {
  width: 100%;
  height: auto;
  padding: 8px 4px;
  border-radius: 5px;
  background: #ff88aa;
}

.margin-top{
  margin-top: 10px;
}


.float-left-top{
  margin-bottom: 20px;
}

.range-slider-style{
  padding: 5px;
}

.firstColumn{
  border-right:1px solid #dddddd;
  left: 0%;
  float:left;
  min-width: 240px;
  max-width: 320px;
  width: 30vw; 
  height: 100vh;
  position: relative;
}

.secondColumn{
  width:auto;
  height: 100vh;
  overflow: hidden;
  position: relative;
}

